
























































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CDatetime from '@/components/links/CDatetime.vue';
import moment from 'moment';
import MomentUtil from '@/typescript/MomentUtil.ts'

@Component({
  components: {
    CDatetime,
  },
  head: {
    title: {
      inner: 'ストップウォッチ',
    },
  },
})
export default class Stopwatch extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataMilliLength: number = 3;
  // stop | play | pause
  private dataState: string = 'stop';
  private dataTimerId: any = 0;
  private dataStartMoment: any = moment(new Date());
  private dataElapsedMoment: any = this.dataStartMoment.clone();
  private dataPointDiffs: {
    flag: string,
    pointDiff: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    },
  }[] = [
    {
      flag: '',
      pointDiff: {
        year: 0,
        month: 0,
        day: 0,
        hour: 0,
        minute: 0,
        second: 0,
        millisecond: 0,
      },
    },
  ];
  private dataHours: number[] = [];
  private dataMinutesAndSeconds: number[] = [];

  // Watch
  @Watch('dataState')
  onStateChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataElapsedMoment')
  onElapsedMomentChange(newValue: string, oldValue: string): void {
    this.diffDatetime();
  }

  // Lifecycle hook
  created(): void {
    this.initHours();
    this.initMinutesAndSeconds();
  }

  // Methods
  private initHours(): void {
    const HOUR_TERM: number = 24;
    for (let i: number = 0; i < HOUR_TERM; i++) {
      this.dataHours.push(i);
    }
  }
  private initMinutesAndSeconds(): void {
    const MINUTE_AND_SECOND_TERM: number = 60;
    for (let i: number = 0; i < MINUTE_AND_SECOND_TERM; i++) {
      this.dataMinutesAndSeconds.push(i);
    }
  }
  private initPointDiff(): void {
    let pointDiff: {
      flag: string,
      pointDiff: {
        year: number,
        month: number,
        day: number,
        hour: number,
        minute: number,
        second: number,
        millisecond: number,
      },
    } = {
      flag: '',
      pointDiff: {
        year: 0,
        month: 0,
        day: 0,
        hour: 0,
        minute: 0,
        second: 0,
        millisecond: 0,
      },
    };
    this.dataPointDiffs.length = 0;
    Vue.set(this.dataPointDiffs, 0, pointDiff);
  }
  private startMoment(): void {
    this.dataStartMoment = moment(new Date());
    this.dataElapsedMoment = this.dataStartMoment.clone();
  }
  private restartMoment(): void {
    this.dataStartMoment = moment(new Date());
    this.dataElapsedMoment = this.dataStartMoment.clone();
    this.dataStartMoment = MomentUtil.subtractPointDiffToDateMoment(this.dataPointDiffs[0].pointDiff, this.dataStartMoment);
  }
  private traceElapsedMoment(): void {
    this.dataTimerId = setInterval((): void => {
      this.dataElapsedMoment = moment(new Date());
      this.diffDatetime();
    }, 1);
  }
  private ringChime(): void {
    let ringElem: HTMLAudioElement = <HTMLAudioElement>document.getElementById('ring');
    ringElem.currentTime = 0;
    ringElem.play();
  }
  private stopElapsedMoment(): void {
    clearInterval(this.dataTimerId);
  }
  private diffDatetime(): void {
    let nowMoment: any = this.dataElapsedMoment.clone();
    Vue.set(this.dataPointDiffs, 0, this.pickPointUp(nowMoment, ''));
  }
  private pushPointDiff(flag: string): void {
    let nowMoment: any = this.dataElapsedMoment.clone();
    this.dataPointDiffs.push(this.pickPointUp(nowMoment, flag));
  }
  private pickPointUp(nowMoment: any, flag: string): {
      flag: string,
      pointDiff: {
        year: number,
        month: number,
        day: number,
        hour: number,
        minute: number,
        second: number,
        millisecond: number,
      },
    } {
    let pointDiff: {
      flag: string,
      pointDiff: {
        year: number,
        month: number,
        day: number,
        hour: number,
        minute: number,
        second: number,
        millisecond: number,
      },
    } = {
      flag: flag,
      pointDiff: {
        year: 0,
        month: 0,
        day: 0,
        hour: 0,
        minute: 0,
        second: 0,
        millisecond: 0,
      },
    };
    pointDiff.pointDiff = MomentUtil.diffDateMomentsToTotalPointDiff(this.dataStartMoment, nowMoment, 'hour');
    return pointDiff;
  }
  private listenPlay($event: Event): void {
    if (this.dataState === 'stop') {
      this.initPointDiff();
      this.startMoment();
    } else {// pause
      this.restartMoment();
    }
    this.dataState = 'play';
    this.traceElapsedMoment();
  }
  private listenPause($event: Event): void {
    this.dataState = 'pause';
    this.stopElapsedMoment();
    this.pushPointDiff('pause');
  }
  private listenStop($event: Event): void {
    if (this.dataState !== 'stop') {
      this.dataState = 'stop';
      this.stopElapsedMoment();
      this.pushPointDiff('stop');
    }
  }
  private listenFlag($event: Event): void {
    this.pushPointDiff('flag');
  }
  private calcLapTime(
    index: number,
    pointDiff: {
      flag: string,
      pointDiff: {
        year: number,
        month: number,
        day: number,
        hour: number,
        minute: number,
        second: number,
        millisecond: number,
      },
    }
  ): string {
    let lapTimePoint: {
      flag: string,
      pointDiff: {
        year: number,
        month: number,
        day: number,
        hour: number,
        minute: number,
        second: number,
        millisecond: number,
      },
    } = {
      flag: pointDiff.flag,
      pointDiff: {
        year: pointDiff.pointDiff.year,
        month: pointDiff.pointDiff.month,
        day: pointDiff.pointDiff.day,
        hour: pointDiff.pointDiff.hour,
        minute: pointDiff.pointDiff.minute,
        second: pointDiff.pointDiff.second,
        millisecond: pointDiff.pointDiff.millisecond,
      },
    };
    if (1 < index) {
      let nowMoment: any = moment(new Date());
      let previousMoment: any = MomentUtil.addPointDiffToDateMoment(this.dataPointDiffs[index - 1].pointDiff, nowMoment);
      let presentMoment: any = MomentUtil.addPointDiffToDateMoment(pointDiff.pointDiff, nowMoment);
      lapTimePoint.pointDiff = MomentUtil.diffDateMomentsToTotalPointDiff(previousMoment, presentMoment, 'hour');
    }
    return String(lapTimePoint.pointDiff.hour) + ':'
      + this.padZero(lapTimePoint.pointDiff.minute, 2) + ':'
      + this.padZero(lapTimePoint.pointDiff.second, 2) + '.'
      + this.padZero(lapTimePoint.pointDiff.millisecond, this.dataMilliLength);
  }
  private padZero(number: number, length: number): string {
    return ('0'.repeat(length) + String(number)).slice(-length);
  }
}
